






















import { Vue, Component, Prop, Ref } from 'vue-property-decorator';
import Icon from '@commonComponents/Icon.vue';

@Component({
  name: 'Avatar',
  components: {
    Icon,
  },
})
export default class extends Vue {
  @Prop({ type: String, required: true }) private src!: string;
  @Prop({ type: String, default: '' }) private alt!: string;
  @Prop({ type: [Number, String], default: 75 }) private size!: string | number;

  @Ref('img') private img!: HTMLElement;

  private isError = false;

  private get _size(): string {
    return typeof this.size === 'number' ? `${this.size}px` : this.size;
  }

  private onErrorHandle(): void {
    this.isError = true;
  }

  private mounted(): void {
    this.img.addEventListener('error', this.onErrorHandle);
  }

  private beforeDestory(): void {
    this.img.removeEventListener('error', this.onErrorHandle);
  }
}
