






























import { Component, InjectReactive } from 'vue-property-decorator';
import { Articles, Categories } from '@/types/article';
import { UserInfo as TypeUserInfo } from '@/types/userInfo';
import { Website } from '@/types/app';
import Page from '@mixins/Page';
import UserInfo from '@components/UserInfo.vue';
import ArticleCard from '@components/ArticleCard.vue';
import Card from '@commonComponents/Card.vue';
import YButton from '@commonComponents/YButton.vue';

@Component({
  name: 'Home',
  components: {
    ArticleCard,
    UserInfo,
    Card,
    YButton,
  },
})
export default class extends Page {
  @InjectReactive('articles') private articles!: Articles;

  @InjectReactive('userInfo') private userInfo!: TypeUserInfo;

  @InjectReactive('categories') private categories!: Categories;

  @InjectReactive('website') private website!: Website;

  private currentCategory: string = '全部';

  private get _articles(): Articles {
    if (this.currentCategory === '全部') {
      return this.articles;
    }

    return this.articles.filter((article) => article.category === this.currentCategory);
  }

  private selectCategory(category: string): void {
    if (this.currentCategory === category) {
      return;
    }

    this.currentCategory = category;
    this.$router.replace({
      query: {
        category,
      },
    });
  }

  private created(): void {
    this.currentCategory = this.$route.query.category as string || '全部';
  }
}
