




















import { Vue, Component, InjectReactive } from 'vue-property-decorator';
import { Link, UserInfo } from '@/types/userInfo';
import { Articles } from '@/types/article';
import Card from '@commonComponents/Card.vue';
import Tag from '@commonComponents/Tag.vue';
import Avatar from '@commonComponents/Avatar.vue';
import Icon from '@commonComponents/Icon.vue';

@Component({
  name: 'UserInfo',
  components: {
    Card,
    Tag,
    Avatar,
    Icon,
  },
  filters: {
    getLink(link: Link): string {
      if (link.type === 'email') {
        return `mailto:${link.value}`;
      }

      return link.value;
    },
  },
})
export default class extends Vue {
  @InjectReactive('userInfo') private userInfo!: UserInfo;
  @InjectReactive('articles') private articles!: Articles;

  private icons: Array<string> = ['weibo', 'zhihu', 'github', 'bilibili', 'email'];
}
