import { Vue, Component } from 'vue-property-decorator';
import { getType } from '@utils/index';
import { isServerRender } from '@utils/is';

const mixin = {
  mounted(): void {
    if (!isServerRender) {
      (this as any).setTitle();
    }
  },
};

@Component({
  mixins: [
    mixin,
  ],
})
export default class extends Vue {
  public getTitle(): string {
    const title = (this as any).$route.meta?.title;
    return getType(title) === 'Function' ? title(this) : title;
  }

  public setTitle(): void {
    const title = this.getTitle();

    if (title) {
      document.title = title;
    }
  }
}
