












import { Vue, Component, Prop } from 'vue-property-decorator';
import ArticleInfo from '@components/ArticleInfo.vue';
import Card from '@commonComponents/Card.vue';
import BaseImage from '@commonComponents/BaseImage.vue';
import RouteName from '@router/routerName';

@Component({
  name: 'ArticleCard',
  components: {
    ArticleInfo,
    Card,
    BaseImage,
  },
})
export default class extends Vue {
  @Prop({ type: String, required: true }) private id!: string;
  @Prop({ type: String, default: '' }) private title!: string;
  @Prop({ type: String, default: '' }) private createTime!: string;
  @Prop({ type: String, default: '' }) private category!: string;
  @Prop({ type: String, default: '' }) private banner!: string;

  private RouteName = RouteName;
}
